.project_card{
padding: 1em;
border: 1px solid grey ;
border-radius: 5px;
width: 24%;
margin: 0.5%;
}

.project_card h4{
    background-color: rgb(13, 4, 92);
    color: rgb(255, 255, 255);
    padding: 0.4em;
    margin-bottom: 1.3em;
    font-size: 1.3em;
}

.project_card p{
    margin-bottom: 1em;
}
.project_card p span{
    font-weight: bold;
}
.category_text{
    display: text;
align-items: center;
}
.category_text span{
    display: inline-block;
    width:12px;
    height: 12px;
    border-radius: 12px;
    background-color: white;
    margin-right: 5px;

}
.category_text .infra{
background-color: rgb(102, 209, 209);
}
.category_text .desenvolvimento{
background-color: rgb(226, 128, 128);
}
.category_text .design{
background-color: rgb(104, 104, 223);
}
.category_text .planejamento{
background-color: rgb(178, 130, 223);
}

.project_card_actions{
margin-top: 1.2em;
display: flex;
align-items: center;
}
.project_card_actions a,
.project_card_actions button{
    text-decoration: none;
    border: none;
    background-color: white;
    color: black;
    font-size: .9em;
    padding: .6em 1em;
    margin-right: 1em;
    cursor: pointer;
    border: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;

}
.project_card_actions svg{
    margin-right: .5em;
}
.project_card_actions a:hover,
.project_card_actions button:hover{
    background-color: rgb(26, 3, 3);
    color: rgb(255, 255, 255);
}
